<template>
  <div class="highlight-container highlight-height px-4 py-6">
    <div class="highlight-wrapper">
      <div class="matches-box">
        <div v-for="(league, index) in matchesHighlights" :key="index">
          <div v-if="matchUp(league) == 'Live'" class="matchUp">
            <div>Live</div>
            <img width="70" height="20" alt="live match" src="/static/image/favourite/now.webp" />
          </div>
          <div v-else-if="matchUp(league) == 'Pre-match'" class="matchUp">
            <div>Pre-Match</div>
          </div>
          <div v-else class="matchUp">
            <div>Match Hot</div>
            <img width="70" height="20" alt="hot match" src="/static/image/favourite/hot.webp" />
          </div>
          <a class="match-item" :class="$vuetify.breakpoint.mdAndUp?'mr-5':'mr-3'" :href="routeName.CRICKET">
            <div class="league-name">{{ league.league_name }}</div>

            <div class="match-team">
              <div class="home-team">
                <div>
                  <img class="country-logo" :src="league.home_logo" />
                </div>
                <div class="team-name">{{ league.home_team }}</div>
                <div class="odds">{{ parseFloat(league.home_odds).toFixed(2) }}</div>
              </div>
              <div class="verDiv">
                <img class="versus" src="/static/image/favourite/versus.png" />
              </div>
              <div class="away-team">
                <div>
                  <img class="country-logo" :src="league.away_logo" />
                </div>
                <div class="team-name">{{ league.away_team }}</div>
                <div class="odds">{{ parseFloat(league.away_odds).toFixed(2) }}</div>
              </div>
            </div>
            <div class="match-datetime-div">
              <div class="match-date">{{ league.matchDate }}</div>
              <div class="match-time">{{ league.matchTime }}</div>
            </div>
          </a>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
import { uiHelper } from '@/util'
import { SHARED } from '@/constants/constants'
import { GAME_MATCHES_HIGHLIGHTS } from '@/store/game.module'
import { ROUTE_NAME } from '@/constants/route.constants'
import orderBy from 'lodash/orderBy'
import take from 'lodash/take'

export default {
  name: 'appHomeHighlight',
  data: () => ({
    highlights: null,
    routeName: ROUTE_NAME,
    desktopHighlights_1: [],
    desktopHighlights_2: [],
    currentHightlight_1: 0,
    currentHightlight_2: 0,
    startTime: SHARED.DEFAULT_START_TIME,
    endTime: SHARED.DEFAULT_END_TIME
  }),
  created() {
    this.getMatchesHighlights()
  },
  computed: {
    matchesHighlights() {
      let k = this.$store.state.game.matchesHighlights.filter(x => x.match_date >= this.startTime)
      k = orderBy(k, ['match_date'], ['asc'])
      k = take(k, 7)
      const n = Math.ceil(k.length / 2)
      this.desktopHighlights_1 = k.slice(0, n)
      this.desktopHighlights_2 = k.slice(n)
      k = k.map(function(league) {
        let leagueTime = league.match_date.indexOf(' ') != -1 ? league.match_date.split(' ')[1] : league.match_date
        let leagueDate = league.match_date.indexOf(' ') != -1 ? league.match_date.split(' ')[0] : league.match_date

        league.matchTime = leagueTime.split(':')[0] + ':' + leagueTime.split(':')[1]
        league.matchDate = leagueDate

        return league
      })

      return k
    }
  },
  /*watch: {
    matchesHighlights() {
      this.highlights = this.$store.state.game.matchesHighlights
      return this.highlights
    }
  },*/
  methods: {
    next_1() {
      this.currentHightlight_1 += 1
    },
    previous_1() {
      if (!this.currentHightlight_1) this.currentHightlight_1 = this.desktopHighlights_1.length - 1
      else this.currentHightlight_1 -= 1
    },
    next_2() {
      this.currentHightlight_2 += 1
    },
    previous_2() {
      if (!this.currentHightlight_2) this.currentHightlight_2 = this.desktopHighlights_2.length - 1
      else this.currentHightlight_2 -= 1
    },
    changeLanguage(language) {
      this.$store.dispatch(`${SHARED.LOADING}`)
      uiHelper.removeCookie('language')
      uiHelper.setCookie('language', language)
      window.location.reload()
    },
    getMatchesHighlights() {
      this.$store.dispatch(`${GAME_MATCHES_HIGHLIGHTS}`)
    },
    matchUp(league) {
      if (moment(league.match_date).isSame(new Date(), 'day')) {
        return 'Live'
      } else if (moment(league.match_date).isAfter(new Date(), 'day')) {
        return 'Pre-match'
      } else {
        return 'Hot'
      }
    }
  }
}
</script>

