        :plugins="this.plugins"
      >
        <div v-for="(banner, i) in bannerContent" :key="i" class="mx-1 flicking-panel mob_banner">
          <img :src="banner.mobile_banner" width="320" height="150" :alt="banner.desc" class="krikya_panel" @click="callBannerAction(banner)" />
