      >
        <!-- <v-carousel class="home-banner-carousel-mobile hidden-sm-and-down" delimiter-icon="mdi-minus" :show-arrows="false" cycle interval="10000" height="auto" height="420px"> -->
        <v-carousel-item v-for="(banner, i) in bannerContent" :key="i">
          <img class="desktop-banner" :src="`${banner.desktop_banner}`" :alt="banner.desc" @click="callBannerAction(banner)" />
          <!-- <v-img :src="`${banner.desktop_banner}`" @click="callBannerAction(banner.action)" :aspect-ratio="1920/467"> -->
          <!-- <v-overlay class="banner-overlay" :absolute="true" :value="true" :opacity="0">
                    <div class="ma-10">
                        <h1>{{banner.title}}</h1>
                        <p>{{banner.desc}} </p>
                        <app-button class="apply-button" :action="callBannerAction" :title="$t(`label.applyAsAffiliateAgent`)"></app-button>
                    </div>
                </v-overlay> -->
          <!-- </v-img> -->
        </v-carousel-item>
      </v-carousel>
      <!-- <app-sport-casino-holder></app-sport-casino-holder> -->
    </div>

    <div class="marquee-row">
      <div class="marquee-wrapper">
        <div class="marquee-icon">
          <img src="/static/svg/announcement-icon.svg" width="25" height="25" style="background-color: #378f6a" />
        </div>
        <!-- This stays fixed on the left -->
        <div :class="$vuetify.breakpoint.mdAndUp ? 'marqueeDesktop' : 'marqueeMobile'">
          <div class="marquee-content">
            <template v-for="announcement in filteredAnnouncement">
              <span :key="announcement.id" class="">
                <label class="d-inline-block home-announcment-content">{{ announcement.title }}</label>
                <label class="d-inline-block home-announcment-content" v-html="announcement.message ? ' :' + announcement.message.replace(/<[p>]*>/g, ' ') : ''"></label>
              </span>
