<script>
import { uiHelper } from '@/util'
import { SHARED } from '@/constants/constants'
import { ROUTE_NAME } from '@/constants/route.constants'
import { REFERRAL_GET_OVERVIEW } from '@/store/referral.module'
import VueQrCode from 'vue-qrcode-component'

export default {
  name: 'ReferralHomeBanner',
  components: {
    VueQrCode
  },
  data: () => ({
    mediaUrl: SHARED.MEDIA_SERVER_URL,
    language: uiHelper.getLanguage(),
    routeName: ROUTE_NAME,
    overview: {},
    share_url: ''
  }),
  created() {
    this.getMemberReferralOverview()
  },
  computed: {
    webShareApiSupported() {
      return navigator.share
    },
    isLoggedIn() {
      return this.$store.state.member.isLoggedIn
    },
    referralOverview() {
      return this.$store.state.referral.overview
    }
  },
  watch: {
    isLoggedIn() {
      this.getMemberReferralOverview()
    },
    referralOverview() {
      this.overview = this.$store.state.referral.overview
      if (window.location.host.toLowerCase() === '16896kk332.com') {
        this.share_url = 'https://www.krikya11.com' + '?refer_code=' + this.overview.my_referral_code
      }
      else if (window.location.host.toLowerCase() === 'ab8945621.com') {
        this.share_url = 'https://www.krikya11.com' + '?refer_code=' + this.overview.my_referral_code
      } else {
        // If not, construct the share URL normally
        this.share_url = uiHelper.getHostname() + '?refer_code=' + this.overview.my_referral_code
      }
    }
  },
  methods: {
    referralCodeCopy() {
      // console.log('referral code appendIcon CLICKED,refer code : '+ this.overview.my_referral_code)
      navigator.clipboard.writeText(this.overview.my_referral_code)
    },
    share() {
      const sharing = {
        title: 'Refer a friend now and earn commission',
        text: 'Your referral code is ' + this.overview.my_referral_code + '.Refer a friend now and earn commission now! ',
        url: this.share_url
      }
      if (this.webShareApiSupported) {
        navigator.share(sharing)
      } else {
        let $f = this
        this.$copyText(this.share_url).then(function(e) {
          $f.alertMessage = 'Referral Link Copied!'
          $f.alertSuccess = true
          $f.alertShow = true
        })
      }
    },
    getMemberReferralOverview() {
      if (this.isLoggedIn) {
        this.$store.dispatch(`${REFERRAL_GET_OVERVIEW}`)
      }
    },
    routeToReferralPage() {
      if (this.isLoggedIn) {
        this.$router.push({
          name: ROUTE_NAME.REFERRAL_SUMMARY
        })
      } else {
        this.$parent.openLoginDialog()
      }
    },
    routeToReferralReport() {
      this.$router.push({
        name: ROUTE_NAME.REFERRAL_REPORT
      })
    }
  }
}
</script>

